.navbar ul li:hover{
    background-color: white;
}
.navbar ul li{
    float: left;
    list-style: none;
    box-sizing: border-box;
    margin: 5px;
    margin-top: auto;
    text-align: center;
    justify-content: center;
    display: flex;
    align-items: center;
}
.navbar ul li h1{
    float: left;
    list-style: none;
    box-sizing: border-box;
    text-decoration: none;
    margin: 20px;
    background-color: black;
    width: 200px;
    height: 40px;
    text-align: center;
    justify-content: center;
    display: flex;
    align-items: center;
    border-radius: 10px;
    border-color: white;
    color: white;
}

.navbar{
    text-align: center;
    justify-content: center;
    display: flex;
}